import Vue from 'vue'
import App from './App.vue'
import router from './router'

import BootstrapVue from 'bootstrap-vue'
Vue.use(BootstrapVue)
import 'bootstrap/scss/bootstrap.scss'

Vue.config.productionTip = false
var vm = new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



