<template>
<div class="home">
  <form>
    <div class="form-group">
      <label for="exampleInputEmail1">名稱</label>
      <input class="form-control" v-model='newMessage.userName'>
    </div>
    <div class="form-group">
      <label for="exampleInputPassword1">訊息</label>
      <input class="form-control" v-model='newMessage.message'>
    </div>
    <button class="btn btn-primary" v-on:click.prevent='sendMessage'>送出</button>
  </form>
  <b-modal v-model='view.showModal1'>
    <p class="my-4">送出訊息時,名稱和訊息都不得為空白.</p>
  </b-modal>
  <ul class="list-group">
    <li class="list-group-item" v-for='item in orderedMessages'>
      <code>{{ item.userName }}:</code> {{ item.message }}
      <code>{{ item.sendTime }}</code>
    </li>
  </ul>
</div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import _ from 'lodash'

const msgRef = firebase.database().ref('/vue-app003/messages/');
export default {
  name: 'home',
  components: {
    HelloWorld
  },
  data: function() {
    return {
      messages: [],
      view: {
        showModal1: false
      },
      newMessage: {
        userName: '',
        message: ''
      }
    }
  },
  computed: {
    orderedMessages: function() {
      const vm = this;
      return _.orderBy(vm.messages, ['sendTime'], ['desc']);
    }
  },
  methods: {
    sendMessage() {

      const vm = this;



      if ((!vm.newMessage.userName || 0 === vm.newMessage.userName.length) ||
        (!vm.newMessage.message || 0 === vm.newMessage.userName.message)) {
        vm.view.showModal1 = true;
      } else {
        msgRef.push({
          userName: vm.newMessage.userName,
          message: vm.newMessage.message,
          sendTime: vm.getTime()
        });
      }

    },
    getTime() {
      const now = new Date();
      const hours = now.getHours();
      const minutes = now.getMinutes();
      const seconds = now.getSeconds();
      const format = (hours >= 12) ? "下午" : "上午";
      return `${format} ${hours}:${minutes}:${seconds}`;
    },
  },
  mounted() {
    const vm = this;
    msgRef.on('value', function(snapshot) {
      const val = snapshot.val();
      vm.messages = val;
    });
  },
}
</script>



